/*******************
Topbar
*******************/
.topbar {
  padding: 0;
  transition: 0.2s ease-in;
  width: 100%;
  position: fixed;
  z-index: 20;
  &.fixed-header {
    box-shadow: $card-shadow;
    transition: 0.2s ease-in;
    background: $white;
    .navbar .navbar-brand img {
      height: 50px;
      -webkit-backface-visibility: hidden;
      -webkit-transform: translateZ(0) scale(1.0, 1.0);
    }
  }
}

/*******************
Main & Page wrapper
*******************/
.page-wrapper {
  padding-top: 0;
}

#main-wrapper {
  overflow: hidden;
}


/*******************
container fluid
*******************/

.container-fluid {
  padding: 0;
}


/*******************
footer
*******************/

.footer {
  padding: 20px 0;
  text-align: center;
}


/*******************
Fix-width
*******************/

.fix-width {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}


/*******************
Spacer
*******************/

.spacer {
  padding: 90px 0;
}

.mini-spacer {
  padding: 40px 0;
}


/*******************
Title
*******************/

.title {
  margin: 20px 0 15px;
}


/*******************
Subtitle
*******************/

.subtitle {
  color: $muted;
  line-height: 24px;
}


/*******************
card
*******************/

.card {
  border: 0;
  margin-bottom: 30px;
  &.card-shadow {
    box-shadow: $card-shadow;
  }
}

.card-group .card {
  border: 1px solid $border;
  margin-bottom: 30px;
}


/*******************
Icon space
*******************/

.icon-space {
  margin: 20px 0;
}

.linking {
  color: $headingtext;
  i {
    font-size: 14px;
    margin-left: 10px;
    transition: 0.2s ease-in;
  }
  &:hover {
    color: $themecolor;
    i {
      margin-left: 15px;
    }
  }
}


/*******************
Listing
*******************/

.list-block {
  margin: 0;
  padding: 0;
  li {
    list-style: none;
    padding: 10px 0;
    display: flex;
    i {
      margin-right: 10px;
      flex-shrink: 0;
      margin-top: 3px;
    }
  }
  &.with-underline {
    li {
      border-bottom: 1px solid $border;
      padding: 15px 0;
    }
  }
}


/*******************
Listing
*******************/

.img-inline {
  margin: 0;
  padding: 0;
  li {
    list-style: none;
    &.half-width {
      width: 49%;
      display: inline-block;
      vertical-align: top;
    }
  }
}


/*******************
Image shadow
*******************/

.img-shadow {
  box-shadow: $img-shadow;
}


/*******************
Image shadow
*******************/

.icon-round {
  width: 80px;
  line-height: 80px;
  text-align: center;
  border-radius: 100%;
  display: inline-block;
}

.no-shrink {
  flex-shrink: 0;
}


/*******************
up section
*******************/

.up {
  z-index: 10;
  position: relative;
}

.bt-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 100;
  visibility: hidden;
}


/*******************
both space
*******************/

.both-space {
  margin: 80px 0;
}


/*******************
Side text content
*******************/

.side-content {
  padding: 40px 60px;
}


/*******************
background cover
*******************/

.bg-cover {
  background-size: cover;
  min-height: 200px;
}


/*******************
Owl carousel
*******************/

.owl-carousel {
  display: block;
  width: 100%;
  .owl-stage-outer {
    position: relative;
    overflow: hidden;
  }
  .owl-stage {
    position: relative;
  }
  .owl-item {
    float: left;
  }
  .owl-nav.disabled {
    display: none;
  }
}


/*******************
Max width
*******************/

.max-300 {
  max-width: 300px;
}

.max-400 {
  max-width: 400px;
}

.max-500 {
  max-width: 500px;
}

.max-600 {
  max-width: 600px;
}


/*******************
line sticks
*******************/

.stick-bottom {
  position: relative;
  margin-bottom: 30px;
  >span {
    width: 30px;
    height: 2px;
    position: absolute;
    bottom: -7px;
    left: 0;
    display: inline-block;
  }
}


/*******************
position
*******************/

.po-absolute {
  position: absolute;
}

.po-relative {
  position: relative;
}


/*-------------------*/


/*Bootstrap 4 hack*/


/*-------------------*/

.invisible {
  visibility: hidden !important;
}

.hidden-xs-up {
  display: none !important;
}

@media (max-width: 575px) {
  .hidden-xs-down {
    display: none !important;
  }
}

@media (min-width: 576px) {
  .hidden-sm-up {
    display: none !important;
  }
}

@media (max-width: 767px) {
  .hidden-sm-down {
    display: none !important;
  }
}

@media (min-width: 768px) {
  .hidden-md-up {
    display: none !important;
  }
}

@media (max-width: 991px) {
  .hidden-md-down {
    display: none !important;
  }
}

@media (min-width: 992px) {
  .hidden-lg-up {
    display: none !important;
  }
}

@media (max-width: 1199px) {
  .hidden-lg-down {
    display: none !important;
  }
}

@media (min-width: 1200px) {
  .hidden-xl-up {
    display: none !important;
  }
}

.hidden-xl-down {
  display: none !important;
}


/*************/


/* Dropdown*/


/*************/

