/*************/
/* General Listing*/
/*************/

.general-listing {
  padding: 0;
  margin: 0;
  li {
    list-style: none;
    a {
      color: $bodytext;
      display: flex;
      padding: 10px 0;
      transition: 0.2s ease-in;
      align-items: center;
    }
    &:hover a {
      color: $themecolor;
      padding-left: 10px;
    }
    i {
      margin-right: 7px;
      vertical-align: middle;
    }
  }
  &.two-part {
    li {
      width: 49%;
      display: inline-block;
    }
  }
  &.only-li {
    li {
      padding: 5px 0;
    }
  }
}


/*************/


/* form dark*/


/*************/

.form-control-dark {
  background: $border;
  color: $white;
  border-color: $border;
  &:focus {
    background: $border;
    color: $white;
  }
}


/*************/


/* social-round */


/*************/

.round-social {
  a {
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    display: inline-block;
    border-radius: 100%;
    margin: 0 5px;
    transition: 0.2s ease-in;
    &:hover {
      transform: translate3d (0, -5px, 0);
    }
  }
  &.light {
    a {
      color: $dark;
      background: $light;
    }
    a:hover {
      background: $dark;
      color: $white;
    }
  }
}


/*************/


/* Form Banner */


/*************/

.banner {
  position: relative;
  max-height: 700px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
  width: 100%;
  display: table;
  .banner-content {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
  }
}

@media (max-width: 992px) {
  .banner {
    height: auto !important;
    padding: 100px 0;
  }
}

@media (max-width: 767px) {
  .banner {
    padding: 30px 0;
  }
}

