
@import "../content/headers/sm-menu/sm-core-css";

.navbar-brand {
  img {
    height: 56px;
  }
  @media only screen and (max-width: 768px) {
    padding: 10px 0;
    img {
      height: 30px;
    }
  }
}

.h12-topbar {
  .phone {
    .icon {
      margin-right: 5px;
    }
  }
  .nav {
    @media only screen and (max-width: 768px) {
      .phone {
        display: block;
        width: 100%;
      }
    }
    .nav-link {
      padding: .5rem .5rem;
    }
  }
}

.client2 {
  padding: 40px 0;
  img {
    height: 40px;
    filter: grayscale(100%);
    transition: all 0.5s;
    &:hover {
      filter: grayscale(0);
    }
  }
  .client-box {
    margin: 0;
    [class*="col-"] {
      padding-top: 10px;
      padding-bottom: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.footer1 {
  position: relative;
  border-top: 1px solid rgba(darken(white, 50%), 0.1);
  .general-listing {
    a {
      font-size: 0.75rem;
      padding: 2px 0;
    }
  }
  .f1-bottom-bar{
    position: relative;
    z-index: 5;
  }
  &:before {
    content: " ";
    right: 0;
    bottom:0;
    position: absolute;
    z-index: 2;
    height: 25rem;
    width: 25rem;
    background: url("../images/logo/netgd-icon.svg");
    background-size: 35rem;
    opacity: 0.05;
  }
}

.feature24 {
  margin-top: -100px;
  .wrap-feature-24 {
    background-color: white;
    position: relative;
    z-index: 5;
    padding-top: 20px;
    .card {
      @include transition(all 1s);
      &:hover{
        transform: scale(0.95, 0.95);
        -webkit-transform: scale(0.95, 0.95);
      }
    }
    &:before {
      content: " ";
      position: absolute;
      top:0;
      width: 100%;
      height: 100px;
      @include box-shadow(0,-20px,50px,rgba(0,0,0,.10));
    }
  }
}

.tp-caption-theme {
  z-index: 6;
  font-weight: 500;
  letter-spacing: -0.05rem;
  strong {
    font-weight: 700;
  }
}

.hesperiden .tp-bullet {
  background: $themecolor !important;
  &.selected {
    background: $themecolor-dark !important;
  }
}